import React, {Component} from 'react';
import HomeLayout from '../components/home-layout';
import Categories from '../../categories/components/categories';
import Related from '../components/related.js';
import ModalContainer from '../../modal/container/Modalcontainer';
import Modal from '../../modal/components/Modal';
import Player from '../../player/container/player';
//redux
import {connect} from 'react-redux';
class Home extends Component{
  state = {
    visibility:false,
    handleError:false,
    media:{}
  }
    handleCloseModalClick = (e) => {
      this.setState({
        visibility:false,
      })
    }
    handleOpenModalClick = (mediaProps) => {
      this.setState({
        visibility:true,
        //Si la propiedad del objecto tiene el mismo
        //nombre que su valor, se puede colocar una sola vez
        media:mediaProps
      })
    }
    componentDidCatch(error, info){
      this.setState({
        handleError: true,
      })
    }
    render(){
      if (this.state.handleError) {
        return (
          <p>Hay un error :c</p>
        );
      }
      /**
       Al conectarse con el store, las propiedades no vienen del atributo
       data del container de home, sino de la función mapStateToProps y de las propiedades
       que retorne, por ejemplo:
       <Categories categories={this.props.data.categories} handleClick={this.handleOpenModalClick}/>
       ahora es:<Categories categories={this.props.categories} handleClick={this.handleOpenModalClick}/>
       */
        return(
            <HomeLayout>
                <Related />
                <Categories
                categories={this.props.categories}
                handleClick={this.handleOpenModalClick}
                search={this.props.search}/>
              {
                this.state.visibility &&
                <ModalContainer>
                  <Modal handleClick={this.handleCloseModalClick}>
                    <Player autoplay src={this.state.media.src} title={this.state.media.title}/>
                  </Modal>
                </ModalContainer>
              }
            </HomeLayout>
        );
    }
}
//connect sirve para conectar nuestro store con nuestro Componente
//Connect recibe varios parámetros entre ellos la función mapStateToProps
function mapStateToProps(state, props){
  /**
  * La función mapStateToProps recibe dos parámetros, el estado del store y las propiedades
  que le llegan al componente envuelto con la función connect
  * No confundir en el state del store con el state del componente, en la función mapStateToProps
  trabaja con el estado del store.
  El return de esta función es un objeto
  *El mapStateToProps solo se usa si alguno de los componentes necesita datos del store, en caso de que no se Así
  se debe buscar otra de las funciones del método connect
  */
  return {
    //La data viene del state del store, no del componente container de home
    categories:state.data.categories,
    search:state.search
  }
}
//Solo los containers se pueden conectar al store
export default connect(mapStateToProps)(Home);



