import React from 'react';
import'./progress.css';
function ProgressBar(props){
	return(
		<div className="Progress">
			<input type="range" min={0} max={props.durationProgress}
			value={props.valueProgress} onChange={props.handleProgressChange}/>
		</div>
	)
}
export default ProgressBar;



