import React, {Component} from 'react';
import PlayerLayout from '../components/player';
import Video from '../components/video';
import Title from '../components/title';
import PlayPausa from '../components/play-pause';
import Timer from '../components/Timer';
import Controls from '../components/controls';
import ProgressBar from '../components/progressBar';
import Spinner from '../components/spinner';
import Volume from '../components/volume';
import FullScreen from '../components/FullScreen';
class Player extends Component{
	state = {
		//A medida que se necesiten estados es importante agregarlos
		pause:false,
		duration:'00:00',
		durationProgress:0,
		valueProgress:0,
		currentTime:'00:00',
		loading:false,
	}
	//end state

	handleTogglePlay=(e)=>{
		this.setState({
			pause:!this.state.pause
		})
	}
	//end handleTogglePlay

	handleLoadedMetadata=(e)=>{
		this.video = e.target;
		let $minutesDuration;
		if(this.video.duration%60 >= 10){
			 $minutesDuration =Math.floor(this.video.duration/60) + ':' + Math.floor(this.video.duration%60)
		}else{
			 $minutesDuration = Math.floor(this.video.duration/60) + ':' +'0'+ Math.floor(this.video.duration%60)
		}
		//this se refiere a la clase en la que estamos
		this.setState({
			duration:$minutesDuration,
			durationProgress:this.video.duration,
		});
		//Los estados son importante para mostrar los datos en algún lado de la UI
		//target es el elemento que envía el evento
	}
	//end handleLoadedMetadata

	handleTimeUpdate=(e)=>{
		let $minutesCurrentTime;

		if(this.video.currentTime%60 >= 10){
			 $minutesCurrentTime = Math.floor(this.video.currentTime/60) + ':' + Math.floor(this.video.currentTime%60)
		}else{
			 $minutesCurrentTime = Math.floor(this.video.currentTime/60) + ':' +'0'+ Math.floor(this.video.currentTime%60)
		}

		this.setState({
			currentTime: $minutesCurrentTime,
			valueProgress:this.video.currentTime
		});
	}
	//end handleTimeUpdate

	handleProgressChange=(e)=>{
		this.video.currentTime= e.target.value
	}//end handleProgressChange

	handleSeeked=(ev)=>{
		this.setState({
			loading:false,
		});
	}//end handleSeeked

	handleSeeking=(ev)=>{
		this.setState({
			loading:true,
		});
	}//end handleSeeking

	handleVolumeChange=(ev)=>{
		this.video.volume = ev.target.value;
	}//end handleVolumeChange

	handleFullscreenClick=(ev)=>{
		if (!document.webkitIsFullScreen) {
			this.player.webkitRequestFullscreen();
		} else {
			document.webkitExitFullscreen()
		}
	}

	componentDidMount(){
		//Los setState cambian el estado
		this.setState({
			pause:!this.props.autoplay
		})
	}
	// end componentDidMount
setRef=(el)=>{
	this.player = el;
}
	render(){
		return(
			<PlayerLayout setRef={this.setRef}>
				<Title title={this.props.title}/>
				<Video
				src={this.props.src}
				autoplay={this.props.autoplay}
				pause={this.state.pause}
				handleLoadedMetadata={this.handleLoadedMetadata}
				handleTimeUpdate={this.handleTimeUpdate}
				handleSeeking={this.handleSeeking}
				handleSeeked={this.handleSeeked}
				/>
				<Spinner active={this.state.loading}/>
				<Controls>
					<PlayPausa pause={this.state.pause} handleClick={this.handleTogglePlay}/>
					<Timer duration={this.state.duration} currentTime={this.state.currentTime}/>
					<ProgressBar durationProgress={this.state.durationProgress}
					valueProgress={this.state.valueProgress} handleProgressChange={this.handleProgressChange}
					/>
					<Volume handleVolumeChange={this.handleVolumeChange}/>
					<FullScreen handleFullscreenClick={this.handleFullscreenClick}/>
				</Controls>
			</PlayerLayout>
		);
	}
}
export default Player;



