import React, {Component} from 'react';
import Search from '../components/search.js';
import {connect} from 'react-redux';
/**
En este caso connect se usa para obtener el método dispatch de redux o
react-redux, pero también debemos conectar este componente con connect, para que
también use los datos que vienen del Provider, es decir, del store.
*/
class SearchContainer extends Component {
	handleSubmit = (e) => {
		e.preventDefault();
		console.log(this.input.value);
		//Se llama a this.props.dispatch, ya que el dispatch viene por propiedades a través de connect
		//El dispatch envía el type(obligatorio) y el payload, que tiene que ver con la acción a ejecutar
		this.props.dispatch({
			type:'SEARCH_VIDEO',
			payload:{
				query: this.input.value
			}//end payload
		});//end dispatch

		this.input.value = '';
	}//end handleSubmit

	setInputRef = (element) => {
		this.input = element;
	}

	//.replace(' ', '-') reemplaza espacio por guiones, reemplaza el primer parámetro por el segundo
render(){
	return(
		<Search handleSubmit={this.handleSubmit} setRef={this.setInputRef} />
	);
}
}
//Solo los containers se conectan al store
//este componente solo necesita el método dispatch y no el state del stora, así que puedo dejar el connect sin parámetros
export default connect()(SearchContainer);



