import React from 'react';
import Category from './category';
import './category.css';
import './searchResults.css';
import SearchContainer from '../search/container/search';
import Media from '../../playlist/components/media.js';

function Categories(props){
    return(
    <div className="categories">
        <SearchContainer />
      {props.search.length > 0 &&
        <div className="Resultados">
          <h2>Resultados de tu búsqueda:</h2>
          <div className="searchResults">
            {
            props.search.map((item,key)=>{
              return(
                <Media {...item} key={key} handleOpenModalClick={props.handleClick}/>
              );
            })
          }
          </div>
        </div>
      }

     {
                props.categories.map((item)=>{
                    return (
                      <Category key={item.id} {...item}
                      handleClick={props.handleClick}/>
                    );
                })
            }
        </div>
    )
}

export default Categories;



