import React from 'react';
import './search.css';
//Los values se sustituyen por defaultValue a menos que se quiera usar el manejador onChange
function Search(props){
	return(
	<div className="search">
		<form className="Search" onSubmit={props.handleSubmit}>
			<input className="Search-input" type= "text" placeholder="Buscar..." ref={props.setRef}/>
		</form>
	</div>
	);
}
export default Search;



