//Este reducer maneja los datos de mi aplicación
//Los reducers son siempre funciones puras y recibe dos parámetros:el state y la action
function dataReducer(state, action){
	//El type es obligatorio dentro de una acción, y es mandado por el método dispatch
	//De hecho el dispatch manda toda la acción, en este caso es una búsqueda que se encuentra en ./categories/search
	//El reducer siempre debe actualizar el estado
	//El estado a actualizar debe ser un objeto, ya que el initialState fue declarado objeto
	switch (action.type) {
		case 'SEARCH_VIDEO':
		//Los tres puntos iteran el objeto con un key
		//action.payload.query
		/**
		Otra forma de hacerlo
		let results = []
		state.data.categories.forEach( category => {
			results = results.concat(
				category.playlist.filter(
					item => item.author
					        .includes( action.payload.query )
				)
			)
		})
		*/
		let results = []
		if(action.payload.query){
			const list = state.data.categories;
				list.forEach( category => {
					//.concat agrega los resultados al array results
					//Nota, al colocar las funciones con paréntesis, se les añade el return por default
					results = results.concat(
						category.playlist.filter((item)=>{
							return item.author.toLowerCase().includes(action.payload.query.toLowerCase());
							//Filter va a comparar cada item con el elemento de la consulta, si marca verdadero, lo agrega al array
						})//endfilter
					)
				})//endforEach

				list.forEach( category => {
					//.concat agrega los resultados al array results
					//Nota, al colocar las funciones con paréntesis, se les añade el return por default
					results = results.concat(
						category.playlist.filter((item)=>{
							return item.title.toLowerCase().includes(action.payload.query.toLowerCase());
							//Filter va a comparar cada item con el elemento de la consulta, si marca verdadero, lo agrega al array
						})//endfilter
					)//endconcat
				})//endforeach
			}//endif
			return {
				...state,
				search:results
			};
			break;
		default:
		return state;
	}
}
export default dataReducer;



