import React,{Component} from 'react';
import './video.css';

//autoPlay va en camelCase en react
class Video extends Component{
	togglePlay(){
		if(this.props.pause){
			this.video.play();
		}else{
			this.video.pause();
		}
	}
	componentWillReceiveProps(props){
		if(props.pause !== this.props.pause){
			this.togglePlay();
		}
	}
	setRef = (el) => {
		this.video = el;
	}
	//onLoadedMetadata es un método de react para obtener metadatos de los videos

	render(){
		const {
			handleLoadedMetadata,
			handleTimeUpdate,
			handleSeeked,
			handleSeeking
		} = this.props;
		//Esto quiere decir que handleTimeUpdate y handleLoadedMetadata son métodos que vienen de las props
		return(
			<div className="Video">
				<video
				id="video"
				src={this.props.src}
				autoPlay={this.props.autoplay}
				ref={this.setRef}
				onLoadedMetadata={handleLoadedMetadata}
				onTimeUpdate={handleTimeUpdate}
				onSeeking={handleSeeking}
				onSeeked={handleSeeked}
			/>
			</div>
		);
	}
}
export default Video;



