import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import Home from './pages/containers/home';
import data from './api.json';
import registerServiceWorker from './registerServiceWorker';
//Redux
import {createStore} from 'redux';
import {Provider} from 'react-redux';
import dataReducer from './reducers/data.js';
/**
 Los componentes funcionales, al no tener ciclo de vida, son menos eficientes a la otra de controlar su reactividad y el re-renderizado.
Por cierto, las funciones puras … en el enfoque de la Programación Funcional son aquellas funciones que única y exclusivamente existen para retornar un valor (y no hacer otra cosa), a partir de sus parámetro de entrada …
Así que de alguna manera, los componentes funcionales, son a la vez que componentes, también funciones puras
---------
PureComponent: tiene el método shouldComponentUpdate ya asignado (por defecto), si a este componente no se le actualizan las propiedades, no tenemos que validar a mano con shouldComponentUpdate, PureComponent lo hace por nosotros, es decir; si recibe nuevas propiedades pero son las que ya teniamos, no se re-renderiza.
import React, { PureComponent } from 'react';
class Playlist extends PureComponent{
  render() {
    <Componente />
    }
}
------------
Componente Funcional: Es una función la cual solo retorna el JSX de nuestro componente (renderiza UI), es mas sencillo, mas fácil de probar y este componente no tiene ciclo de vida.
import React from'react';
function Playlist(props) {
  return (<Componente title={props.title} />)
}
Presentacional Cómo se ve

-Puede contener smart components u otros componentes de UI
-Permiten composición con `[props.children]``
-No depeden del resto de la aplicación
-No especifica cómo los datos son cargados o mutados
-Recibe datos y callbacks solo con propiedades
-Rara vez tienen su propio estado
-Están escritos como componentes funcionales a menos que necesiten mejoras de performance. Sólo pueden ser Componentes funcionales o Pure Components

Containers Qué hace

*Concetrado en el funcionamiento de la aplicación
*Contienen componentes de UI u otros containers
*No tienen estilos
*Proveen de datos a componentes de UI u otros contenedores
*Proveen de callbacks a la UI
*Normalmente tienen estado
*Llaman acciones
*Generados por higher order components
*/
const initialState = {
	//Este es el estado de nuestro store
	data:{
		...data,
	},
	//Al estado inicial se le pueden colocar múltiples atributos según las funciones de mi app
	 search: [],
	 notFound: '',
}
//createStore crea el store de Redux
//recibe una función: reducer, el initialState: nuestra data y el enhancer: extensiones
const store = createStore(
	dataReducer,
	initialState
	//,Enhancer
)
//Con getState obtenemos el estado de nuestro store
console.log(store.getState());
//Sin redux:ReactDOM.render(<Home data={data}/>, document.getElementById('root'));
//El provider envuelve al componente de los datos del store, que luego utiliza connect para conectarse con
//los mismos, cada vez que se quiere usar algo de redux en la app, se llama a connect para connectar con el
//provider
ReactDOM.render(
	<Provider store={store}>
		<Home />
	</Provider>, document.getElementById('root'));
registerServiceWorker();



