import React from 'react';
import logo from '../../playlist/components/images/logo.png';
import './related.css';
function Related(props){
    return(
        <div className="Related">
          <a href="/">
            <img src={logo} alt="logo" width={250}/>
          </a>
        </div>
    );
}
export default Related;



